export const mergeSort= (array,low,high,animations,copy)=>{
if(low<high)
{
const mid=Math.floor((low+high)/2)
mergeSort(copy,low,mid,animations,array);
mergeSort(copy,mid+1,high,animations,array)
merge(array,low,mid,high,animations,copy);
}
}
function merge(array,low,mid,high,animations,copy)
{


var j=mid+1;
var i=low;
var k=low;
while(i<=mid&&j<=high)
{
animations.push([i,j])
animations.push([i,j])

if(copy[i]>copy[j])
{

array[k]=copy[j];
animations.push([k, copy[j]]);
++k;
++j;
}
else
{
array[k]=copy[i];
animations.push([k, copy[i]]);
++k;
++i;
}
}

while(i<=mid)
{
animations.push([i,i])
animations.push([i,i])
animations.push([k, copy[i]]);
array[k]=copy[i];

++k;

++i;
}
while(j<=high)
{
animations.push([j,j])
animations.push([j,j])
animations.push([k, copy[j]]);
array[k]=copy[j];

++k;

++j;
}


}

export const quickSort= (array,low,high,animations)=>{
if(low<high)
{
var pp=partition(array,low,high,animations);

quickSort(array,low,pp-1,animations);
quickSort(array,pp+1,high,animations);
}
}

function partition(array,low,high,animations)
{
var e=low;
var f=high;
var num=array[low];

while(1)
{
while(1)
{
if(array[e]>num||e===high)break;
++e;
}
while(1)
{
if(array[f]<=num||f===low)break;
--f;
}
if(e<f)
{
animations.push([e,f])
animations.push([e,f])

var t=array[e];
array[e]=array[f];
array[f]=t;
animations.push([array[e],array[f]])

}
else
{

animations.push([low,f]);
animations.push([low,f]);

var t1=array[low];
array[low]=array[f];
array[f]=t1;

animations.push([array[low],array[f]])

break;

}

}
return f;
}

export const heapSort= (array,animations) =>{
var g,m,k;

for(var i=1;i<array.length;++i)
{

m=i;
do
{

k=parseInt((m-1)/2);
animations.push([k,m]);
animations.push([k,m]);
if(array[k]<array[m])
{

g=array[k];
array[k]=array[m];
array[m]=g;

}
animations.push([array[k],array[m]]);
m=k;
}while(m!=0)


}
for(var i=array.length-1;i>=0;--i)
{
animations.push([0,i]);
animations.push([0,i]);
g=array[i];
array[i]=array[0];
array[0]=g;
animations.push([array[0],array[i]]);
k=0;

do{
m=2*k+1;
if(m<i)animations.push([k,m]);
if(m<i)animations.push([k,m]);
if(m<(i-1)&&array[m]<array[m+1])++m;
if(m<i&&array[k]<array[m])
{

g=array[k];
array[k]=array[m];
array[m]=g;


}
if(m<i)animations.push([array[k],array[m]]);
k=m;
}while(m<i)



}


}

export const bubbleSort= (array,animations)=>{
var m=array.length-1;
var e,f;
while(m>0)
{
for(var j=0;j<m;++j)
{
e=j;
f=e+1;
animations.push([e,f]);
animations.push([e,f]);

if(array[e]>array[f])
{
var t=array[e];
array[e]=array[f];
array[f]=t;
}
animations.push([array[e],array[f]]);


}
--m;

}
}
