export const insertionSort= (array,animations) =>{
var p;
var num;
var j;
var z=0
for(var i=1;i<array.length;++i)
{
z=0;
p=i;
j=p-1;
num=array[i];z=0;
while(j>=0)
{
animations.push([j,p]);
animations.push([j,p]);


if(array[j]<=num){z=1;break;}

array[p]=array[j];
animations.push([p,array[j]]);
animations.push([p,array[j]]);
--p;--j;
}
array[p]=num;
if(j==-1)
{
animations.push([0,0]);
animations.push([0,0]);
animations.push([p,num]);
animations.push([p,num]);
}
else
{
animations.push([p,num]);
animations.push([p,num]);
}
}

}
export const linearSort= (array,animations) =>{

for(var i=0;i<array.length;++i)
{
for(var j=i+1;j<array.length;++j)
{
if(array[i]>array[j])
{
animations.push([i,j]);
animations.push([i,j]);

var t=array[i];
array[i]=array[j];
array[j]=t;
animations.push([array[i],array[j]]);

}
}
}




}


export const selectionSort= (array,animations) =>{

var m;
for(var i=0;i<array.length-1;++i)
{
m=i;
for(var j=i+1;j<array.length;++j)
{

if(array[m]>array[j])m=j;

}
animations.push([i,m]);
animations.push([i,m]);

var t=array[m];
array[m]=array[i];
array[i]=t;
animations.push([array[i],array[m]]);

}


}

