import React,{useState} from 'react';
import * as sortingAlgorithms from '../sortingAlgorithms/sortingAlgorithm';

import Select from 'react-select';
import Slider from 'react-input-slider';
import NumericInput from 'react-numeric-input';
import './SortingVisualizer.css';

const pricolor='black';
const seccolor='white';
const midcolor='red';
export default class SortingVisualizer extends React.Component {


constructor(props){
super(props);

this.state={
array:[],
x:0,
speed:1000,
num:40,
selectedOption:null,
val:1,
};

}

componentDidMount()
{
this.resetArray();
}
insertionSort()
{
const animations=[]
sortingAlgorithms.insertionSort(this.state.array,animations)
for(let i=0;i<animations.length;++i)
{
const arraybars=document.getElementsByClassName('array-bar');

if((i%4)<2)
{

const [bar1,bar2]=animations[i];
const barStyle1=arraybars[bar1].style;

const barStyle2=arraybars[bar2].style;
const color=i%4 ===0?seccolor:pricolor;
setTimeout(()=>{
barStyle1.backgroundColor=color;
barStyle2.backgroundColor=color;

},i*this.state.speed)

}
else if(i%4==2)
{
setTimeout(()=>{
const [bar1,bar2]=animations[i];
const barStyle=arraybars[bar1].style;
barStyle.width= `${bar2}px`;

},i*this.state.speed)


}
}


}
shellSort(){
const animations=[]
sortingAlgorithms.shellSort(this.state.array,animations)
for(let i=0;i<animations.length;++i)
{
const arraybars=document.getElementsByClassName('array-bar');

if((i%4)<2)
{

const [bar1,bar2]=animations[i];
const barStyle1=arraybars[bar1].style;

const barStyle2=arraybars[bar2].style;
const color=i%4 ===0?seccolor:pricolor;
setTimeout(()=>{
barStyle1.backgroundColor=color;
barStyle2.backgroundColor=color;

},i*this.state.speed)

}
else if(i%4==2)
{
setTimeout(()=>{
const [bar1,bar2]=animations[i];
const barStyle=arraybars[bar1].style;
barStyle.width= `${bar2}px`;

},i*this.state.speed)


}
}


}

selectionSort()
{
const animations=[]
sortingAlgorithms.selectionSort(this.state.array,animations)
for(let i=0;i<animations.length;++i)
{
const arraybars=document.getElementsByClassName('array-bar');
const isColorChange=i%3!==2;
if(isColorChange)
{
const [bar1,bar2]=animations[i];
const barStyle1=arraybars[bar1].style;
const barStyle2=arraybars[bar2].style;
const color=i%3===0?seccolor:midcolor;
setTimeout(()=>{
barStyle1.backgroundColor=color;
barStyle2.backgroundColor=color;

},i*this.state.speed)

}
else
{
setTimeout(()=>{
const [bar1,bar2]=animations[i-1];
const [width1,width2]=animations[i];
const barStyle1=arraybars[bar1].style;
const barStyle2=arraybars[bar2].style;
barStyle1.width=`${width1}px`;
barStyle2.width=`${width2}px`;
barStyle1.backgroundColor=pricolor;
barStyle2.backgroundColor=pricolor;


},i*this.state.speed)


}
}


}
heapSort()
{
const animations=[]
sortingAlgorithms.heapSort(this.state.array,animations)
console.log(this.state.array)
for(let i=0;i<animations.length;++i)
{
const arraybars=document.getElementsByClassName('array-bar');
const isColorChange=i%3!==2;
if(isColorChange)
{
const [bar1,bar2]=animations[i];

const barStyle1=arraybars[bar1].style;
const barStyle2=arraybars[bar2].style;
const color=i%3===0?seccolor:midcolor;
setTimeout(()=>{
barStyle1.backgroundColor=color;
barStyle2.backgroundColor=color;

},i*this.state.speed)

}
else
{
setTimeout(()=>{
const [bar1,bar2]=animations[i-1];
const [width1,width2]=animations[i];
const barStyle1=arraybars[bar1].style;
const barStyle2=arraybars[bar2].style;
barStyle1.width=`${width1}px`;
barStyle2.width=`${width2}px`;
barStyle1.backgroundColor=pricolor;
barStyle2.backgroundColor=pricolor;


},i*this.state.speed)


}
}


}
linearSort()
{
const animations=[]
sortingAlgorithms.linearSort(this.state.array,animations)
for(let i=0;i<animations.length;++i)
{
const arraybars=document.getElementsByClassName('array-bar');
const isColorChange=i%3!==2;
if(isColorChange)
{
const [bar1,bar2]=animations[i];
const barStyle1=arraybars[bar1].style;
const barStyle2=arraybars[bar2].style;
const color=i%3===0?seccolor:midcolor;
setTimeout(()=>{
barStyle1.backgroundColor=color;
barStyle2.backgroundColor=color;

},i*this.state.speed)

}
else
{
setTimeout(()=>{
const [bar1,bar2]=animations[i-1];
const [width1,width2]=animations[i];
const barStyle1=arraybars[bar1].style;
const barStyle2=arraybars[bar2].style;
barStyle1.width=`${width1}px`;
barStyle2.width=`${width2}px`;
barStyle1.backgroundColor=pricolor;
barStyle2.backgroundColor=pricolor;


},i*this.state.speed)


}
}
}
bubbleSort()
{
const animations=[]
sortingAlgorithms.bubbleSort(this.state.array,animations)
for(let i=0;i<animations.length;++i)
{
const arraybars=document.getElementsByClassName('array-bar');
const isColorChange=i%3!==2;
if(isColorChange)
{
const [bar1,bar2]=animations[i];
const barStyle1=arraybars[bar1].style;
const barStyle2=arraybars[bar2].style;
const color=i%3===0?seccolor:midcolor;
setTimeout(()=>{
barStyle1.backgroundColor=color;
barStyle2.backgroundColor=color;

},i*this.state.speed)

}
else
{
setTimeout(()=>{
const [bar1,bar2]=animations[i-1];
const [width1,width2]=animations[i];
const barStyle1=arraybars[bar1].style;
const barStyle2=arraybars[bar2].style;
barStyle1.width=`${width1}px`;
barStyle2.width=`${width2}px`;
barStyle1.backgroundColor=pricolor;
barStyle2.backgroundColor=pricolor;


},i*this.state.speed)


}
}

}

quickSort()
{
const animations=[]

sortingAlgorithms.quickSort(this.state.array,0,this.state.array.length-1,animations);

for(let i=0;i<animations.length;++i)
{
const arraybars=document.getElementsByClassName('array-bar');
const isColorChange= i % 3 !==2;
if(isColorChange)
{
const [bar1,bar2]=animations[i];
const barStyle1=arraybars[bar1].style;
const barStyle2=arraybars[bar2].style;
const color= (i%3===0)?seccolor:midcolor;
 	
setTimeout(()=>{
barStyle1.backgroundColor=color;
barStyle2.backgroundColor=color;


},i*this.state.speed);


}
else{
setTimeout(()=>{

const [bar1,bar2]=animations[i-1];
const [bar3,bar4]=animations[i];

const barStyle1=arraybars[bar1].style;
const barStyle2=arraybars[bar2].style;

barStyle1.width= `${bar3}px`;
barStyle2.width= `${bar4}px`;
barStyle1.backgroundColor=pricolor;
barStyle2.backgroundColor=pricolor;

},i*this.state.speed);

}
}
}


mergeSort()
{

const animations=[]

const copy=this.state.array.slice()
sortingAlgorithms.mergeSort(this.state.array,0,this.state.array.length-1,animations,copy);
for(let i=0;i<animations.length;++i)
{
const arraybars=document.getElementsByClassName('array-bar');
const isColorChange= i % 3 !==2;
if(isColorChange)
{
const [bar1,bar2]=animations[i];
const barStyle1=arraybars[bar1].style;
const barStyle2=arraybars[bar2].style;
const color= (i%3===0)?seccolor:pricolor;
 	
setTimeout(()=>{
barStyle1.backgroundColor=color;
barStyle2.backgroundColor=color;


},i*this.state.speed);


}
else{
setTimeout(()=>{
const [bar1,bar2]=animations[i];
const barStyle=arraybars[bar1].style;
barStyle.width= `${bar2}px`;
},i*this.state.speed);

}
}
}

sortArray()
{

if(this.state.selectedOption.value==1)
{
this.heapSort()
}
if(this.state.selectedOption.value==2)
{
this.mergeSort()
  
}
if(this.state.selectedOption.value==3)
{
this.insertionSort()
}
if(this.state.selectedOption.value==4)
{
this.selectionSort()
}
if(this.state.selectedOption.value==5)
{
this.quickSort()
}

if(this.state.selectedOption.value==7)
{
this.linearSort()
}
if(this.state.selectedOption.value==8)
{
this.bubbleSort()
}
if(this.state.selectedOption.value==9)
{
this.shellSort()
}



}
render()
{

  const sortingAlgorithms = [
  { label: "Heap Sort", value: 1 },
  { label: "Merge Sort", value: 2 },
  { label: "Insertion Sort", value: 3 },
  { label: "Selection Sort", value: 4 },
  { label: "Quick Sort", value: 5 },
 
{ label: "Linear Sort", value: 7 },
{ label: "Bubble Sort", value: 8 },
{ label: "Shell Sort", value: 9 },

];


const {array}=this.state;
return (
<div className="main__class">
<div className="sidebar">
<div className='text'>
<h1>Sorting Visualizer</h1>
</div>
<div className="button">
<button onClick={()=>this.resetArray()}>Generate New Array</button></div>
<div className="dropdown">
<h3>Choose Algorithm:</h3>
 <Select onChange={(val)=>{
 
 this.state.selectedOption=val
 }} options={ sortingAlgorithms } placeholder="Sorting Algorithm"/>
      </div>
      <div className="slider">
      <h3>Maintain Speed:</h3>
      <Slider x={this.state.x} onChange={({ x }) => this.setState(state => ({ ...state, x }),()=>{
      
      this.state.speed=((1000-((this.state.x)*10))/5);
      this.state.val=(200-this.state.speed)/2;
      })} /><span>{this.state.val}</span>
      
      </div>
      <div className="increment">
      <h3>Size Of Array:</h3>
      <NumericInput min={0} max={70} value={this.state.num} onChange={(val)=>{
      this.state.num=val;
      this.resetArray()
      }}/>
      
      </div>
      <div className="button__sort">
<button onClick={()=>this.sortArray()}>Sort</button></div>
  </div>
<div className="rightbar">
<div className="array-container">

{array.map((value,idx) => (
<div className="array-bar" 
key={idx}
style={{
backgroundColor: pricolor,
width: `${value}px`
}}></div>))}

</div>




</div>
</div>
);
}



resetArray()
{
const array=[];
for(let i=0;i<this.state.num;++i)
{
array.push(randomIntFromInterval(5,631));
}
this.setState({array});
}


}


function randomIntFromInterval(min,max)
{

return Math.floor(Math.random()*(max-min+1)+min);
}

